<template lang="pug">
  .hello.col-md-8
    h1
      strong.text-danger(v-if='name2')  {{ name1 }}  {{ name2 }}!
      strong.text-danger(v-else) (-) {{ name1 }}!
    span     ,   29   17:00.      ,   17.    .<br><br>
    span        &lt;3 <br><br>
    span           :<br>
    router-link(:to="{ name: 'guests'}")
      span      (        :)<br>
    router-link(:to="{ name: 'gifts'}")
      span     <br>
    router-link(:to="{ name: 'songs'}")
      span       <br>
    router-link(:to="{ name: 'info'}")
      span     <br>
    router-link(:to="{ name: 'photos'}")
      span    (    : )<br>
    router-link(:to="{ name: 'contact'}")
      span      <br>
    router-link(:to="{ name: 'home'}")
      span    <br>
    span <br>
    span ,     .      ,  ,  ,       .
    br
    br
    span  ,   <br>
    br
    .btn-group(role="group" aria-label="...")
      button.btn(type="button" :class="accept == false ? 'btn-danger' : 'btn-default'" @click='setComing(false)')   
      button.btn(type="button" :class="accept ? 'btn-success' : 'btn-default'" @click='setComing(true)') 
    br
    br
    .row(style='display: -webkit-inline-box;')
      .row.col-md-12.section
        .col-xs-5.col-md-4
          img(src="../assets/rings.png")
      .col-xs-12.col-md-4
        img(src="../assets/pigeon.png")
      .col-xs-12.col-md-4
        img(src="../assets/pigeon.png")
</template>

<script>
import invitationApi from '../api/invitationApi';

export default {
  data() {
    return {
      name1: '',
      name2: '',
      accept: null,
      msg: 'sdfasd',
    };
  },
  async created() {
    await this.getGuestData(this.$route.params.id);
  },
  methods: {
    async getGuestData(secret) {
      const data = await invitationApi.getInvitationById(secret);
      if (data && data.length > 0) {
        this.name1 = data[0].name;
        if (data.length > 1) {
          this.name2 = data[1].name;
        }
      } else {
        return;
      }

      this.accept = data[0].accepted;
    },
    async setComing(accepted) {
      await invitationApi.setComingById(this.$route.params.id, accepted);

      await this.getGuestData(this.$route.params.id);
    },
  },
};
</script>
<style scoped>
.btn-group{
  margin-top: 20pt;
}
</style>



